import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class NewsRelated extends Component {
    //Hm ny copy trn mng
    chuyenDoiURL = (str) => {
        // Chuyn ht sang ch thng
        str = str.toLowerCase();
        // xa du
        str = str.replace(/(||||||||||||||||)/g, 'a');
        str = str.replace(/(||||||||||)/g, 'e');
        str = str.replace(/(||||)/g, 'i');
        str = str.replace(/(||||||||||||||||)/g, 'o');
        str = str.replace(/(||||||||||)/g, 'u');
        str = str.replace(/(||||)/g, 'y');
        str = str.replace(/()/g, 'd');
        // Xa k t c bit
        str = str.replace(/([^0-9a-z-\s])/g, '');
        // Xa khong trng thay bng k t -
        str = str.replace(/(\s+)/g, '-');
        // xa phn d -  u
        str = str.replace(/^-+/g, '');
        // xa phn d -  cui
        str = str.replace(/-+$/g, '');
        // return
        return str;
    }

    render() {
        return (
            <div className="card">
                <Link to={"/tin-tuc/" + this.chuyenDoiURL(this.props.tieuDe) + "." + this.props.tinId + ".html"}>
                    <img className="card-img-top" src={this.props.anh} alt="react router demo" />
                </Link>
                <div className="card-body">
                    <h4 className="card-title">{this.props.tieuDe}</h4>
                    <p className="card-text">{this.props.trichDan}</p>
                </div>
            </div>
        );
    }
}

export default NewsRelated;


